}

.back {
  width: auto;
  max-width: 100vw;
  max-height: 90vh;


}



.App {

  min-height: 100vh;
  position: relative;

}

.Home {
  min-height: 92vh;
  position: relative;
  padding: 0vh 10vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Problem {

  padding: 3cm 5vw;
