.ph {
  font-size: 55px;
  font-weight: 600;
  position: relative;
  /* Add position relative to the parent element */
  color: rgb(236, 64, 64);
}

.sh{

  color: rgb(2, 186, 2);
}

.pdiv {
  margin-top: 1cm;
  display: flex;
  justify-content: center;
  column-gap: 2cm;
  align-items: center;
}

.pleft-desc {
  font-size: 45px;
  font-weight: 600;
  text-align: center;
  width: 20cm;
  color: #5e5e5e;
  // border: 1px solid black;
  line-height: 1.6cm;
}

.rote {
  color: rgb(236, 64, 64);
  font-size: 60px;
}

.concept{
  color: rgb(2, 186, 2);
}

.ph::after {
  content: "";
  border-bottom: 5px dashed rgb(236, 64, 64);
  /* Use border-bottom to create a line under the text */
  position: absolute;
  /* Position the pseudo-element relative to the parent with position: relative */
  bottom: 0;
  /* Position it at the bottom of the parent */
  left: 0;
  /* Position it at the left of the parent (adjust as needed) */
  width: 2.5cm;

  /* Make the line span the entire width of the parent */
}

.sh::after{
  border-bottom: 5px dashed rgb(2, 186, 2);
}

.prob {
  width: 10cm;
  height: auto;
}

.home-box {
  // border: 1px solid #243257b7;
  width: 21cm;
  // padding: .0cm .0cm;
  padding: 0cm 0cm .25cm 0cm;
  border-radius: 5px;
  display: flex;
  backdrop-filter: blur(1px);
  flex-direction: column;

  align-items: center;
}

.name {
  color: #7f7f8d;
  font-family: acme;
  letter-spacing: .12cm;
  font-weight: 800;
  font-size: 50px;
}

.tagline {
  color: #bcbccc;
  font-family: acme;
  letter-spacing: .12cm;
  font-weight: 800;
  font-size: 70px;
}

.desc {
  color: #bcbccc;
  font-family: acme;
  font-size: 40px;
  text-align: center;
}

.Home::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-image: url("back.jpg");
  background-repeat: no-repeat;
  background-size: auto 100vh;
  background-position: center center;
  filter: brightness(30%);
  background-color: #3a4c7d;
}

.Nav {
  padding: .2cm 5vw;
}

.NavHome {
  position: relative;
  color: white;
}

.NavHome::before {
  content: "";
  z-index: -1;
  top: 0;
  right: 0;
  bottom: 0;
  /* Add this line to cover the entire height of the parent */
  left: 0;
  position: absolute;
  background-color: #3a4c7d;
  filter: brightness(30%);
}


.start {
  background-color: transparent;
  border: 3px solid #00a0d9;
  background-color: #00a0d9;
  cursor: pointer;
  color: white;
  transition: .3s ease;
  margin-top: 1cm;
  font-family: acme;
  font-size: 25px;
  padding: .17cm .8cm;
  letter-spacing: .03cm;
  border-radius: 10px;

  &:hover {
    background-color: transparent;
  }
}

@media only screen and (max-width:900px) {
  .Home::before {
    background-size: 95vw auto;
  }
}